import React from "react";
import Particles from "react-particles-js";
import {decorations} from './Datas/getData';

export default function BackgroundContainer(props)
{
	return(
     <Particles style={{
		width: "100%",
		height: "100%",
		position: "absolute",
		top: "0",
		left: "0",
		zIndex:0
	  }}
    params={{
	    "particles": {
	        "number": {
	            "value": 8,
	            "density": {
	                "enable": true,
	                "value_area": 200
	            }
	        },
	        "line_linked": {
	            "enable": false
	        },
	        "move": {
	            "speed": 1,
	            "out_mode": "out"
	        },
	        "shape": {
	            "type": "image",
	            "image": decorations[props.color]
	        },
	        "color": {
	            "value": "#CCC"
	        },
	        "size": {
	            "value": 5,
	            "random": false,
	            "anim": {
	                "enable": true,
	                "speed": 4,
	                "size_min": 10,
	                "sync": false
	            }
	        }
	    },
	    "retina_detect": false
	}} />);
}